// -----------------------------------------------------------------------
// Default
// -----------------------------------------------------------------------
* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

// -----------------------------------------------------------------------
// Font
// -----------------------------------------------------------------------
// font-style-stretch-normal
.font-style-stretch-normal {
  font-style: normal;
  font-stretch: normal;
}

// font-weight-normal
.font-weight-normal {
  font-weight: normal;
}

// font-weight-500
.font-weight-500 {
  font-weight: 500;
  font-size: 14px;
}

// font-weight-600
.font-weight-600 {
  font-weight: 600;
}

.upper-case-text {
  text-transform: uppercase;
}

// font-size-11
.font-size-11 {
  font-size: 11px;
}

// font-size-12
.font-size-12 {
  font-size: 12px;
}

// font-size-14
.font-size-14 {
  font-size: 14px;
}

// font-size-16
.font-size-16 {
  font-size: 16px;
}

// line-height-1.33
.line-height-1.33 {
  line-height: 1.33;
}

// letter-spacing-normal
.letter-spacing-normal {
  letter-spacing: normal;
}

// capitalize
.capitalize {
  text-transform: capitalize;
}

// color-#425a70
.color-#425a70 {
  color: #425a70;
}

// color-#234361
.color-#234361 {
  color: #234361;
}

// -----------------------------------------------------------------------
// BulkBookingDetailsOnSearchPage Pay now dialog
// -----------------------------------------------------------------------
#BBDOSP-A1-label-5 {
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #425a70;
}

#BBDOSP-A1-label-6 {
  font-size: 12px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #234361;
}

// -----------------------------------------------------------------------
// Flex
// -----------------------------------------------------------------------
// display-flex
.display-flex {
  display: flex;
}

// row-flex
.row-flex {
  flex-direction: row;
}

// col-flex
.col-flex {
  flex-direction: column;
}

// flex-align-start
.flex-align-start {
  align-items: flex-start !important;
}

// flex-align-end
.flex-align-end {
  align-items: flex-end;
}

// flex-align-center
.flex-align-center {
  align-items: center;
}

// flex-justify-start
.flex-justify-start {
  justify-content: flex-start !important;
}

// flex-justify-end
.flex-justify-end {
  justify-content: flex-end;
}

.margin-bottom-24 {
  margin-bottom: 24px;
}

