// -----------------------------------------------------------------------
// Others
// -----------------------------------------------------------------------

// list-item
.list-item {
  height: 32px;
  padding: 0 !important;
  background-color: transparent !important;
}

// nested-list-item
.nested-list-item,
.nested-list-item-selected {
  padding: 0 !important;
  padding-left: 56px !important;
  height: 32px !important;
  background-color: transparent !important;
}

.nested-list-item-selected {
  background-color: #1070ca !important;
}

//remove-btn
.remove-btn,
.add-extra-btn {
  padding-right: 0 !important;
}

//react datepicker text align
.react-datepicker__input-container input {
  text-align: center;
}

// margin-top-25
.margin-top-25 {
  margin-top: 25px;
}

// letter spacing -0.1px
.letter-spacing-0.1 {
  letter-spacing: -0.1px;
}

// button
.button {
  padding: 0 !important;
  justify-content: center !important;
}

.booking-checkout-text {
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.25;
  letter-spacing: -0.1px;
  color: #234361;
}

.select-field-container select {
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #435a6f;
}

.inline-alert > div:first-child {
  align-self: flex-start;
}

.date-picker {
  //height: 32px !important;
  //display: flex;
  //padding: 0 8px !important;
  border-radius: 3px !important;
  color: rgb(102, 120, 138) !important;
}

.date-picker input {
  //margin: auto !important;
  color: transparent !important;
  text-align: center !important;
  text-shadow: 0 0 0 rgb(102, 120, 138) !important;

  &:focus {
    outline: none !important;
  }
}
