.RPP-A1-wrapper,
.RPP-A1-col-container,
.RPP-A1-period-radio-popover-wrapper,
.RPP-A1-row-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.RPP-A1-row-container {
  flex-direction: row;
}

.RPP-A1-period-radio-popover-wrapper {
  width: 184px;
  height: 112px;
  padding: 8px 0 16px 14px;
}

.RPP-A1-button-as-select {
  padding: 0 16px;
  justify-content: space-between;
  text-transform: capitalize;
}

#RPP-A1-type-one-label {
  font-size: 11px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.3px;
  color: #66788a;
  text-transform: uppercase;
}

// -----------------------------------------------------------------------
// Margin()
// -----------------------------------------------------------------------
.RPP-A1-margin-bottom-8 {
  margin-bottom: 8px;
}

.RPP-A1-margin-bottom-4 {
  margin-bottom: 4px;
}

.RPP-A1-margin-right-32 {
  margin-right: 32px;
}

.RPP-A1-margin-top-24 {
  margin-top: 24px;
}
