.UMDDC-A1-body-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 336px;
  margin: 16px auto 4px auto;
}

#UMDDC-A1-large-label,
.UMDDC-A1-input-container label {
  font-size: 11px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.45;
  letter-spacing: 0.3px;
  color: #66788a;
  text-transform: uppercase;
}

.UMDDC-A1-input-container textarea,
.UMDDC-A1-input-container input {
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  resize: none;
}

.UMDDC-A1-label-container {
  margin-bottom: 8px;
}

.UMDDC-A1-col-elements-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 28px;
}

.UMDDC-A1-row-elements-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.UMDDC-A1-country-select-menu-container {
  margin-right: 8px;
}

.UMDDC-A1-invalid-mobile-num-msg-container {
  margin-top: 8px;
}

.UMDDC-A1-radio-input {
  align-items: center;
}

.UMDDC-A1-radio-input span {
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #435a6f;
}

.UMDDC-A1-date-picker-row {
  padding-left: 24px;
}

.UMDDC-A1-footer-content {
  border-top: 1px solid #e4e7eb;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-top: 16px;
}
